import {Port} from "./port";
import Graphics = PIXI.Graphics;
import Container = PIXI.Container;
import Point = PIXI.Point;

export class PhantomConnection extends Container {
  private curve: Graphics;
  private p1: Port;

  constructor() {
    super();
    this.curve = new Graphics();
    this.addChild(this.curve);
  }

  public hide(): void {
    this.curve.clear();
    this.p1 = null;
  }

  public setPort(p1: Port): void {
    this.p1 = p1;
  }

  public isActive(): boolean {
    return !!this.p1;
  }

  public update(b: PIXI.Point): void {
    if (!this.p1) return;
    b = this.parent.toLocal(b);
    let c2: PIXI.Point;
    let c1: PIXI.Point;
    let a = new PIXI.Point(this.p1.position.x + this.p1.parent.position.x, this.p1.position.y + this.p1.parent.position.y);

    c1 = new PIXI.Point(b.x - (b.x - a.x) / 2, a.y);
    c2 = new PIXI.Point(a.x + (b.x - a.x) / 2, b.y);

    this.curve.clear();
    let vvv = Math.sqrt(Math.pow(a.x - b.x, 2) + Math.pow(a.y - b.y, 2));
    let step = 1 / (vvv / 10);
    let delta = step * 0.3;
    step -= delta;
    for (let i = 0; i <= 1; i += step + delta) {
      let lp1 = PhantomConnection.bezier4(a, c1, c2, b, i);
      let lp2 = PhantomConnection.bezier4(a, c1, c2, b, i + step);

      this.curve.lineStyle(2, 0x111133);
      this.curve.moveTo(lp1.x, lp1.y);
      this.curve.lineTo(lp2.x, lp2.y);
    }
  }

  private static bezier4(p1: PIXI.Point, p2: PIXI.Point, p3: PIXI.Point, p4: PIXI.Point, mu: number): PIXI.Point {
    let mum1: number, mum13: number, mu3: number;
    let p = new PIXI.Point();

    mum1 = 1 - mu;
    mum13 = mum1 * mum1 * mum1;
    mu3 = mu * mu * mu;

    p.x = mum13 * p1.x + 3 * mu * mum1 * mum1 * p2.x + 3 * mu * mu * mum1 * p3.x + mu3 * p4.x;
    p.y = mum13 * p1.y + 3 * mu * mum1 * mum1 * p2.y + 3 * mu * mu * mum1 * p3.y + mu3 * p4.y;

    return p;
  }

  private getLength(p0, p1, p2) {
    const a = new Point(
      p0.x - 2 * p1.x + p2.x,
      p0.y - 2 * p1.y + p2.y
    );
    const b = new Point(
      2 * p1.x - 2 * p0.x,
      2 * p1.y - 2 * p0.y
    );
    const A = 4 * (a.x * a.x + a.y * a.y);
    const B = 4 * (a.x * b.x + a.y * b.y);
    const C = b.x * b.x + b.y * b.y;

    const Sabc = 2 * Math.sqrt(A + B + C);
    const A_2 = Math.sqrt(A);
    const A_32 = 2 * A * A_2;
    const C_2 = 2 * Math.sqrt(C);
    const BA = B / A_2;

    return (A_32 * Sabc + A_2 * B * (Sabc - C_2) + (4 * C * A - B * B) * Math.log((2 * A_2 + BA + Sabc) / (BA + C_2))) / (4 * A_32);
  }
}



