import {EditorObject} from "./editor-object";
import Point = PIXI.Point;
import Container = PIXI.Container;
import {stack} from "d3-shape";
export class Aligner {
  private distance: number = 50;
  private stack: Container[] = [];

  alignHorizontal(objects: EditorObject[]) {
    let y = objects[0].getGlobalPosition().y;
    objects.forEach(x => {
      let p = x.getGlobalPosition();
      p.y = y;
      let newP = x.parent.toLocal(p);
      x.setPosition(newP);
    });
  }

  alignHorizontalCenter(objects: EditorObject[]) {
    let y = objects[0].getGlobalPosition().y + objects[0].height / 2;
    objects.forEach(x => {
      let p = x.getGlobalPosition();
      p.y = y - x.height / 2;
      let newP = x.parent.toLocal(p);
      x.setPosition(newP);
    });
  }

  alignVertical(objects: EditorObject[]) {
    let x = objects[0].getGlobalPosition().x;
    objects.forEach(o => {
      let p = o.getGlobalPosition();
      p.x = x;
      let newP = o.parent.toLocal(p);
      o.setPosition(newP);
    });
  }

  alignVerticalCenter(objects: EditorObject[]) {
    let x = objects[0].getGlobalPosition().x + objects[0].width / 2;
    objects.forEach(o => {
      let p = o.getGlobalPosition();
      p.x = x - o.width / 2;
      let newP = o.parent.toLocal(p);
      o.setPosition(newP);
    });
  }

  distributeHorizontal(objects: EditorObject[], sortByX: boolean = true) {
    if (sortByX) objects = objects.sort(this.sortX);
    for (let i = 1; i < objects.length; i++) {
      let newP = new Point(objects[i - 1].x + objects[i - 1].width + this.distance, objects[i].y);
      objects[i].setPosition(newP);
    }
  }

  distributeVertical(objects: EditorObject[], sortByY: boolean = true) {
    if (sortByY) objects = objects.sort(this.sortY);
    for (let i = 1; i < objects.length; i++) {
      let newP = new Point(objects[i].x, objects[i - 1].y + objects[i - 1].height + this.distance);
      objects[i].setPosition(newP);
    }
  }

  autoAlign(objects: EditorObject[]) {
    let rootObjs = this.getRootObjects(objects);
    let y = 0;
    rootObjs.forEach((x, i) => {
      let newP = new Point(0, y);
      x.setPosition(newP);
      let branchHeight = this.setNext(x, objects);
      y += branchHeight + +this.distance;
    });
  }


  private sortX(a: EditorObject, b: EditorObject): number {
    if (a.x > b.x)return 1;
    else if (a.x < b.x)return -1;
    else return 0;
  }

  private sortY(a: EditorObject, b: EditorObject): number {
    if (a.y > b.y)return 1;
    else if (a.y < b.y)return -1;
    else return 0;
  }

  private getRootObjects(objects: EditorObject[]): EditorObject[] {
    return objects.filter(x => x.getInputPort().connections.length === 0);
  }

  private setNext(x: EditorObject, objects: EditorObject[]): number {
    let delta = 0;
    let ports = x.getOutputPorts().filter(p => p.connections.length > 0);
    ports.forEach(p => {
      p.connections.forEach(c => {
        let node = c.port2.parent;
        let X = x.x + x.width + this.distance;
        let Y = x.y + delta;
        let point = new Point(X, Y);
        delta += node.height + this.distance;
        node.setPosition(point);
        if (node.getOutputPorts().filter(p => p.connections.length > 0).length > 0) this.setNext(node, objects);
      });
    });

    return delta === 0 ? x.height : delta;
  }


}



