import {Component, EventEmitter, OnInit, Output, ViewChild} from '@angular/core';
import {CollapsibleTree} from "./collapsible-tree";
import {AccordionItem} from "@angular/material";
import {DataService} from "../../services/data.service";

@Component({
  selector: 'app-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.scss']
})
export class NavigationComponent implements OnInit {
  @ViewChild('svg') svg;
  @ViewChild(AccordionItem) accordion;
  @Output() navigate: EventEmitter<any> = new EventEmitter();

  constructor(private _dataService: DataService) {
  }

  ngOnInit() {
    let collapsibleTree = new CollapsibleTree();
    collapsibleTree.onNavigate.subscribe(r => {
      if (r.navigatable) {
        this.accordion.close();
        this.navigate.emit(r);
      }
    });
    this._dataService.getNavigation().subscribe(treeData => {
      collapsibleTree.init(this.svg.nativeElement, treeData);
    });
    this._dataService.navigationStructureChanges.subscribe(
      treeData => {
        collapsibleTree.init(this.svg.nativeElement, treeData);
      }
    );
  }
}



