import {Port, PortOrientation} from "./port";
import {EditorObject} from "./editor-object";
import {Table} from "./Layouts/table";
import {Button} from "./button";
import {Requirement} from "./requirement";
import Container = PIXI.Container;
import Graphics = PIXI.Graphics;

export class SimpleBlock extends EditorObject {
  private _box: PIXI.Graphics;
  public inPort: Port;
  public outPort: Port;

  public inputReqs: Requirement[] = [];
  public outputReqs: Requirement[] = [];

  public embeddedContainer: Container;

  blockName: string = 'pupsik\r\nshmupsik\r\nepta';
  blockCode: string = 'ПД.ОДИ.Б1.Э1';
  blockRole: string = 'АР_ВС';
  blockHours: string = '10000';

  constructor() {
    super();
    this.createView();
    this.interactive = true;
    this.addListener('added', e => {
      // this.update();
    });
  }

  addInputReq(req: Requirement): SimpleBlock {
    this.inputReqs.push(req);
    return this;
  }

  addOutputReq(req: Requirement): SimpleBlock {
    this.outputReqs.push(req);
    return this;
  }

  public getPorts(): Port[] {
    return [this.inPort, this.outPort];
  }

  protected createView() {
    // let co = new Container();
    // let c1 = new Condition();
    // c1.addCase('case 1');
    // c1.addCase('case 2');
    // c1.addCase('case 3');
    // c1.position.set(30, 0);
    // let c2 = new Condition();
    // c2.addCase('case 1');
    // c2.addCase('case 2');
    // c2.position.set(300, 30);
    // let cccc = new Connection(c1.outPorts[0], c2.inPort);
    // co.addChild(c1, c2, cccc);

    let table = new Table();
    table.add(new PIXI.Text(this.blockName)).alignCenter().colspan(3);
    table.row();
    // table.add(co).colspan(3).pad(20);
    // table.row();
    table.add(new PIXI.Text(this.blockCode)).alignCenter();//.separtor();
    table.add(new PIXI.Text(this.blockRole)).alignCenter();//.separtor();
    table.add(new PIXI.Text(this.blockHours)).alignCenter();
    table.row();
    this.loadImg(table);
    table.build2();
    table.debug(true);

    let inputReqs = new Table();
    let r = new Requirement(this, 'rere');
    r.color = 0x0000FF;
    inputReqs.add(r);
    inputReqs.row();
    inputReqs.add(new Requirement(this, 'rer1e'));
    inputReqs.row();
    inputReqs.add(new Requirement(this, 'rere2'));
    inputReqs.row();
    inputReqs.add(new Requirement(this, 'rere2'));
    inputReqs.row();
    inputReqs.add(new Requirement(this, 'rere2'));
    inputReqs.row();
    inputReqs.add(new Requirement(this, 'rere2'));
    inputReqs.row();
    inputReqs.add(new Requirement(this, 'rere2'));
    inputReqs.row();
    inputReqs.add(new Requirement(this, 'rere2'));
    inputReqs.row();
    inputReqs.add(new Requirement(this, 'rere2'));
    inputReqs.build2();
    inputReqs.position.set(0, 0);

    let outputReqs = new Table();
    let r1 = new Requirement(this, 'rere', false);
    r1.color = 0xff0000;
    outputReqs.add(r1).alignCenterRight();
    outputReqs.row();
    let r2 = new Requirement(this, 'rer1e', false);
    r2.color = 0xffff00;
    outputReqs.add(r2).alignCenterRight();
    outputReqs.row();
    outputReqs.add(new Requirement(this, 'rere2', false)).alignCenterRight();
    outputReqs.build2();
    outputReqs.position.set(table.width - outputReqs.width + 17, 0);

    table.position.set(0, Math.max(inputReqs.height, outputReqs.height));

    let box = this._box = new PIXI.Graphics();
    let h = table.height + Math.max(inputReqs.height, outputReqs.height);
    let w = table.width;
    box.beginFill(0xCBC6BE);
    box.lineStyle(2);
    box.drawRoundedRect(0, 0, w, h, 10);
    box.endFill();

    let inPort = this.inPort = new Port(this, PortOrientation.Left);
    inPort.x = 0;
    inPort.y = h - table.height / 2;
    inPort.isInput = true;
    let outPort = this.outPort = new Port(this, PortOrientation.Right);
    outPort.x = w;
    outPort.y = h - table.height / 2;

    this.addChild(box, inPort, outPort, inputReqs, outputReqs, table);
  }

  protected update() {
    this.removeChildren();
    this.createEmbeddedContainer();
    let root = this.createMainContent();
    let reqs = this.createReqs(root);
    root.position.set(0, Math.max(reqs[0].height, reqs[1].height));
    let box = this.createBox(root, reqs[0], reqs[1]);
    let ports = this.createPorts(root, reqs[0], reqs[1]);
    this.addChild(ports[0], ports[1], box, ...reqs, root);
  }

  private createReqs(rootTable: Table): Table[] {
    let inputReqs = new Table();
    for (let req of this.inputReqs) {
      inputReqs.add(req);
      inputReqs.row();
    }
    if (inputReqs.cellsCount > 0) inputReqs.build2();
    inputReqs.position.set(0, 0);

    let outputReqs = new Table();
    for (let req of this.outputReqs) {
      outputReqs.add(req);
      outputReqs.row();
    }
    if (outputReqs.cellsCount > 0) outputReqs.build2();
    outputReqs.position.set(rootTable.width - outputReqs.width + 17, 0);

    return [inputReqs, outputReqs];
  }

  private createMainContent(): Table {
    let table = new Table();
    table.addText(this.blockName).alignCenter().colspan(3);
    table.row();
    table.add(this.embeddedContainer).colspan(3).pad(20);
    table.row();
    table.addText(this.blockCode).alignCenter();//.separtor();
    table.addText(this.blockRole).alignCenter();//.separtor();
    table.addText(this.blockHours).alignCenter();
    table.row();
    this.loadImg(table);
    table.build2(true);
    return table;
  }

  private createEmbeddedContainer() {
    this.embeddedContainer = new Container();
    //ToDo сделать заполнение вложенными данными
    // let co = new Container();
    // let c1 = new Condition(3, 'fgsfg');
    // c1.position.set(30, 0);
    // let c2 = new Condition(3, 'fgsfg');
    // c2.position.set(300, 30);
    // let cccc = new Connection(c1.outPorts[0], c2.inPort);
    // co.addChild(c1, c2, cccc);
  }

  private createBox(rootTable: Table, inputReqs: Table, outputReqs: Table): Graphics {
    let box = this._box = new PIXI.Graphics();
    let h = rootTable.height + Math.max(inputReqs.height, outputReqs.height);
    let w = rootTable.width;
    box.beginFill(0xCBC6BE);
    box.lineStyle(2);
    box.drawRoundedRect(0, 0, w, h, 10);
    box.endFill();
    return box;
  }

  private createPorts(rootTable: Table, inputReqs: Table, outputReqs: Table): Port[] {
    let h = rootTable.height + Math.max(inputReqs.height, outputReqs.height);
    let w = rootTable.width;
    let inPort = this.inPort = new Port(this, PortOrientation.Left);
    inPort.x = 0;
    inPort.y = h - rootTable.height / 2;
    inPort.isInput = true;
    let outPort = this.outPort = new Port(this, PortOrientation.Right);
    outPort.x = w;
    outPort.y = h - rootTable.height / 2;
    return [inPort, outPort];
  }

  private loadImg(rootTable: Table) {

    let table = new Table();
    table.add(this.loadSprite('word')).alignCenter();
    table.add(this.loadSprite('pdf')).alignCenter();
    table.add(this.loadSprite('revit')).alignCenter();
    table.add(this.loadSprite('navisworks')).alignCenter();
    table.add(this.loadSprite('excel')).alignCenter();
    table.build2();

    let table1 = new Table();
    table1.add(new Button('picture', () => {
      alert('sdgasfgsfag');
    }));
    table1.add(new Button('youtube', () => {
      window.open('https://youtu.be/nKFDp9Y52-o');
    })).alignCenter();
    table1.add(new Button('table', () => {
    })).alignCenter();
    table1.add(new Button('edu', () => {
    })).alignCenter();
    table1.add(new Button('book', () => {
    })).alignCenter();
    table1.build2();

    rootTable.add(table).alignCenterLeft();
    rootTable.add(table1).alignCenterRight().colspan(2);

  }

  private loadSprite(name: string): PIXI.Sprite {
    let sprite = new PIXI.Sprite(PIXI.loader.resources[name].texture);
    sprite.scale = new PIXI.Point(0.5, 0.5);
    sprite.width = 32;
    sprite.height = 32;
    return sprite;
  }


}



