import {EditorObject} from "./objects/editor-object";
import {Bump} from "../../ext/bump";
import DisplayObject = PIXI.DisplayObject;

export class SelectorFn {
  public static containsSelectorFn(bump: Bump, rect: PIXI.Rectangle): (EditorObject) => boolean {
    return function (object: EditorObject | DisplayObject) {
      if (!(object instanceof EditorObject)) return false;
      let bounds = object.getBounds();
      return rect.x < bounds.x && rect.y < bounds.y && (rect.x + rect.width > bounds.x + bounds.width) &&
        (rect.y + rect.height > bounds.y + bounds.height);
    }
  };

  public static intersectSelectorFn(bump: Bump, rect: PIXI.Rectangle): (EditorObject) => boolean {
    return function (object: EditorObject | DisplayObject) {
      if (!(object instanceof EditorObject)) return false;
      return bump.hitTestRectangle(rect, object.getBounds());
    }
  };
}



