import Container = PIXI.Container;
import {Port, PortOrientation} from "./port";
import {EditorObject} from "./editor-object";
import {Table} from "./Layouts/table";

export class Requirement extends Container {
  private isInput: boolean = true;
  port: Port;
  private text: PIXI.Text;
  title: string;

  set color(c: number) {
    this.port.color = c;
  }

  constructor(parent: EditorObject, text: string, isInput: boolean = true) {
    super();
    this.isInput = isInput;
    this.text = new PIXI.Text(text);
    this.title = text;

    let c = new Container();
    this.port = isInput ? new Port(parent) : new Port(parent, PortOrientation.Right);
    this.port.isInput = isInput;
    this.port.position.set(0, 10);
    c.width = 10;
    c.height = 10;
    c.addChild(this.port);

    let table = new Table();
    if (this.isInput) {
      table.add(c).alignCenterLeft();
      table.add(this.text).alignCenterLeft();
    } else {
      table.add(this.text).alignCenterRight().pad(3);
      table.add(c).alignCenterRight();
    }
    table.build2();
    this.addChild(table);
  }
}



