import {Injectable} from '@angular/core';
import {Subject} from "rxjs/Subject";
import {NavigationRecord} from "../models/navigation-record";
import {Observable} from "rxjs/Observable";
import {BlockType} from "../components/editor/objects/block-type";
import * as _ from "lodash";
import "rxjs/add/observable/of";

@Injectable()
export class DataService {
  private _navigationStructureChanges: Subject<NavigationRecord> = new Subject();
  public navigationStructureChanges: Observable<NavigationRecord> = this._navigationStructureChanges.asObservable();

  constructor() {
  }

  public getNavigation(): Observable<NavigationRecord> {
    return Observable.of(this.makeNavigationData());
  }

  private makeNavigationData(): NavigationRecord {
    function makeChildren(parentId: string, data: any[]): NavigationRecord[] {
      let d = data.filter(x => x.parent === parentId && x.type != BlockType.Element);
      let children = d.map(x => {
        let navigationRecord = new NavigationRecord();
        navigationRecord.id = x.id;
        navigationRecord.name = x.name;
        navigationRecord.code = x.code;
        navigationRecord.navigatable = true;
        navigationRecord.children = makeChildren(x.id, data);
        return navigationRecord;
      });
      return children;
    }

    let root = this.data.filter(x => !x.parent)[0];
    let nd = new NavigationRecord();
    nd.id = root.id;
    nd.name = root.name;
    nd.code = root.code;
    nd.navigatable = false;
    nd.children = makeChildren(root.id, this.data);
    return nd;
  }

  private data = [
    {id: '1', name: 'ПИК', code: 'PIK', type: BlockType.SuperBlock, kind: 'Tech', parent: null},
    {id: '2', name: 'МП', code: 'MP', type: BlockType.SuperBlock, kind: 'WorkType', parent: '1'},
    {id: '3', name: 'АК', code: 'AK', type: BlockType.SuperBlock, kind: 'WorkType', parent: '1'},
    {id: '4', name: 'СЭМ 2', code: 'SEM2', type: BlockType.SuperBlock, kind: 'WorkTypeVariant', parent: '2'},
    {id: '5', name: 'версия 1', code: 'V1', type: BlockType.SuperBlock, kind: 'WorkTypeVariantVersion', parent: '4'},
    {id: '6', name: 'версия 2', code: 'V2', type: BlockType.SuperBlock, kind: 'WorkTypeVariantVersion', parent: '4'},
    {id: '7', name: 'AR', code: 'AR', type: BlockType.SuperBlock, kind: 'WorkSection', parent: '5'},
    {id: '8', name: 'KR', code: 'KR', type: BlockType.SuperBlock, kind: 'WorkSection', parent: '5'},
    {id: '9', name: 'Б1', code: 'B1', type: BlockType.Block, kind: 'StageSet', parent: '7'},
    {id: '10', name: 'Э1', code: 'Э1', type: BlockType.Element, kind: 'Stage', parent: '9'},
    {id: '11', name: 'Э2', code: 'Э2', type: BlockType.Element, kind: 'Stage', parent: '9'},
    {id: '12', name: 'Э3', code: 'Э3', type: BlockType.Element, kind: 'Stage', parent: '9'},
  ];

  public getSubtree(id: string) {
    let getByParent = (parentId) => {
      let d = this.data.filter(x => x.parent === parentId);
      return d.concat(_.flatten(d.map(x => getByParent(x.id))));
    };
    let root = this.data.filter(x => x.id === id)[0];
    return {root: root, data: getByParent(id)};
  }
}



