import {Port} from "./port";
import Graphics = PIXI.Graphics;
import Sprite = PIXI.Sprite;
import "rxjs/add/operator/startWith";

export class Connection extends PIXI.Container {
  private deleteButton: Sprite;
  private curve: Graphics;
  public port1: Port;
  public port2: Port;
  private btnScale: number = 1;
  private ticker = 0;
  private isSelected = false;
  private fun = (delta) => this.tick(delta);

  constructor(_p1: Port, _p2: Port) {
    super();
    this.deleteButton = new Sprite(PIXI.loader.resources.del.texture);
    this.deleteButton.width = 32;
    this.deleteButton.height = 32;
    this.deleteButton.pivot = new PIXI.Point(16, 16);
    this.deleteButton.interactive = true;
    this.deleteButton.buttonMode = true;
    this.deleteButton.on('click', e => {
      this.parent.removeChild(this);
      this.clearPorts();
      this.hideDelBtn();
    });

    this.interactive = true;
    let [p1, p2] = _p2.isInput ? [_p1, _p2] : [_p2, _p1];
    this.port1 = p1;
    this.port2 = p2;

    p1.connections.push(this);
    p2.connections.push(this);

    this.curve = new Graphics();
    this.addChild(this.curve);
    p1.parent.onPositionChanged.merge(p2.parent.onPositionChanged).startWith(null).subscribe(e => {
      this.update();
    });
  }

  public setSelected(value: boolean): void {
    this.isSelected = value;
    if (value) {
      this.showDelBtn();
    } else {
      this.hideDelBtn();
    }
    this.update();
  }

  public clearPorts() {
    let index = this.port1.connections.indexOf(this);
    if (index !== -1) {
      this.port1.connections.splice(index, 1);
    }
    index = this.port2.connections.indexOf(this);
    if (index !== -1) {
      this.port2.connections.splice(index, 1);
    }
  }

  private update(): void {
    let c2: PIXI.Point;
    let c1: PIXI.Point;
    let a = new PIXI.Point(this.port1.position.x + this.port1.parent.position.x, this.port1.position.y + this.port1.parent.position.y);
    let b = new PIXI.Point(this.port2.position.x + this.port2.parent.position.x, this.port2.position.y + this.port2.parent.position.y);

    if (a.x < b.x) {
      c1 = new PIXI.Point(b.x - (b.x - a.x) / 2, a.y);
      c2 = new PIXI.Point(a.x + (b.x - a.x) / 2, b.y);
    } else if (a.x >= b.x) {
      c1 = new PIXI.Point(a.x + 350, a.y);
      c2 = new PIXI.Point(b.x - 350, b.y);
    }

    this.curve.clear();
    this.curve.lineStyle(this.isSelected ? 5 : 3, this.isSelected ? 0xff4500 : 0x3355ee);
    this.curve.moveTo(a.x + 10, a.y);
    this.curve.bezierCurveTo(c1.x, c1.y, c2.x, c2.y, b.x - 10, b.y);
    this.calcBtnPosition();
  }

  private getMiddlePoint(): PIXI.Point {
    let point = new PIXI.Point();
    let a = this.port1.getGlobalPosition();
    let b = this.port2.getGlobalPosition();
    point.x = (a.x + b.x) / 2;
    point.y = (a.y + b.y) / 2;
    return this.toLocal(point);
  }

  private showDelBtn() {
    this.calcBtnPosition();
    this.addChild(this.deleteButton);
    PIXI.ticker.shared.remove((this.fun));
    PIXI.ticker.shared.add(this.fun);
  }

  private hideDelBtn() {
    this.removeChild(this.deleteButton);
    PIXI.ticker.shared.remove(this.fun);
    this.ticker = Math.random() * 100;
  }

  private calcBtnPosition() {
    let p = this.getMiddlePoint();
    this.deleteButton.position.set(p.x, p.y);
  }

  private tick(delta: number) {
    this.btnScale = (Math.sin(this.ticker += delta / 20) + 1) * 0.15 + 0.6;
    this.deleteButton.scale.set(this.btnScale, this.btnScale);
  }

}



