import {EditorObject} from "./editor-object";
import Graphics = PIXI.Graphics;
import "rxjs/add/operator/merge";

export class Connection extends PIXI.Container {
  private _a: EditorObject;
  private _b: EditorObject;
  private graphics: PIXI.Graphics;
  private _lastDifference = {x: 0, y: 0};

  constructor(a: EditorObject, b: EditorObject) {
    super();
    this._a = a;
    this._b = b;
    let graphics = this.graphics = new Graphics();

    this.addChild(graphics);
    this.update();
    a.onMove.merge(b.onMove).subscribe(() => this.update());
  }

  private update() {
    let graphics = this.graphics;
    let a = this._a.getGlobalPosition();
    let b = this._b.getGlobalPosition();
    // let diff = {x: a.x - b.x, y: a.y - b.y};
    // console.log(diff, this._lastDifference);
    // if(diff.x === this._lastDifference.x && diff.y === this._lastDifference.y) {
    //   return;
    // } else {
    //   this._lastDifference = diff;
    // }
    //let d = Math.sqrt(Math.pow(this._a.x - this._b.x, 2) + Math.pow(this._a.y - this._b.y, 2));
    graphics.clear();
    graphics.lineStyle(2, 0x33FF00);
    graphics.moveTo(a.x, a.y);
    graphics.lineTo(b.x, b.y);
  }
}



