import {Component, OnInit} from '@angular/core';
import {Subject} from "rxjs/Subject";
import {ViewSet} from "../editor/objects/view-set";
import {DataService} from "../../services/data.service";
import {NavigationRecord} from "../../models/navigation-record";
import {ViewGroup} from "../editor/objects/view-group";
import {ViewElement} from "../editor/objects/view-element";

@Component({
  selector: 'app-tech-editor',
  templateUrl: './document-editor.component.html',
  styleUrls: ['./document-editor.component.scss']
})
export class DocumentEditorComponent implements OnInit {
  public itemsSource: Subject<ViewSet> = new Subject();
  public selectionSource: Subject<any[]> = new Subject();

  constructor(private _dataService: DataService) {
  }

  ngOnInit(): void {

  }

  onNavigate(r: NavigationRecord) {
    this.itemsSource.next(this.makeViewSet(this._dataService.getSubtree(r.id)));
  }

  onEditorSelection(selection: any[]) {
    this.selectionSource.next(selection);
  }

  // zaglooshka
  private makeViewSet(r) {
    let vs = new ViewSet();
    vs.viewId = r.root.id;
    for (let gi = 0; gi < Math.random() * 2 + 1; gi++) {
      let viewGroup = new ViewGroup();
      viewGroup.position = new PIXI.Point(Math.random() * 1000, Math.random() * 1000);
      for (let i = 0; i < Math.random() * 10 + 1; i++) {
        let ve = new ViewElement();
        ve.position = new PIXI.Point(Math.random() * 1000, Math.random() * 1000);
        viewGroup.elements.push(ve);
      }
      vs.elements.push(viewGroup);
    }
    return vs;
  }
}



