import {Subject} from "rxjs/Subject";
import {Observable} from "rxjs/Observable";
import {PositionChangedArgs} from "../events/position-changed-args";
import {Port} from "./port";
import {GlowFilter} from "../glow";

export abstract class EditorObject extends PIXI.Container {
  protected isSelected = false;
  protected _positionChangedSubject: Subject<PositionChangedArgs> = new Subject();
  public onPositionChanged: Observable<PositionChangedArgs> = this._positionChangedSubject.asObservable();
  private outlineFilter = new GlowFilter(10, 2, 0, 0x0099ff, 0.5);

  constructor() {
    super();
  }

  public setPosition(point: PIXI.Point) {
    this.x = point.x;
    this.y = point.y;
    this._positionChangedSubject.next(new PositionChangedArgs(point.clone()));
  }

  public setSelected(isSelected: boolean) {
    if (isSelected && !this.isSelected) {
      this.selectConnections();
      this.filters = [this.outlineFilter];
    } else if (!isSelected && this.isSelected) {
      this.deselectConnections();
      this.filters = [];
    }
    this.isSelected = isSelected;
  }

  public get selected() {
    return this.isSelected;
  }

  protected createView() {

  }

  public selectConnections() {
    let ports = this.children.filter(x => x instanceof Port)
      .map(x => x as Port)
      .filter(x => x.connections.length > 0);

    ports.forEach(x => {
      x.connections.forEach(c => c.setSelected(true));
    });
  }

  public deselectConnections() {
    let ports = this.children.filter(x => x instanceof Port)
      .map(x => x as Port)
      .filter(x => x.connections.length > 0);

    ports.forEach(x => {
      x.connections.forEach(c => c.setSelected(false));
    });
  }

  public hasConnenctions(): boolean {
    return this.children.filter(x => x instanceof Port)
        .map(x => x as Port)
        .filter(x => x.connections.length > 0).length > 0;
  }

  public getInputPort(): Port {
    return this.children.filter(x => x instanceof Port)
      .map(x => x as Port)
      .filter(x => x.isInput)[0];
  }

  public getOutputPorts(): Port[] {
    return this.children.filter(x => x instanceof Port)
      .map(x => x as Port)
      .filter(x => !x.isInput);
  }

  public abstract getPorts(): Port[];
}



