import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {
  MdButtonModule, MdCardModule, MdCheckboxModule, MdDialogModule, MdExpansionModule, MdFormFieldModule, MdIconModule,
  MdInputModule,
  MdSidenavModule
} from "@angular/material";

@NgModule({
  imports: [
    CommonModule,
    MdButtonModule,
    MdCardModule,
    MdDialogModule,
    MdIconModule,
    MdFormFieldModule,
    MdCardModule,
    MdInputModule,
    MdCheckboxModule
  ],
  exports: [
    MdButtonModule,
    MdCardModule,
    MdDialogModule,
    MdExpansionModule,
    MdSidenavModule,
    MdIconModule,
    MdFormFieldModule,
    MdCardModule,
    MdInputModule,
    MdCheckboxModule
  ],
  declarations: []
})
export class MaterialModule { }



