import Container = PIXI.Container;
import {Cell} from "./cell";

export class Table extends Container {
  private curCol: number = 0;
  private curRow: number = 0;

  cells: Cell[][] = [[]];

  get cellsCount(): number {
    return this.cells.reduce((a, b) => a.concat(b)).length;
  }

  get columnsCount(): number {
    return this.cells.map(x => x.length)
      .reduce((a, b) => Math.max(a, b));
  }

  get rowsCount(): number {
    return this.cells.length;
  }


  add(obj: Container): Cell {
    let cell = new Cell(this, this.curCol, this.curRow);
    cell.add(obj);
    this.cells[this.curRow].push(cell);
    this.curCol++;
    this.addChild(cell);
    return cell;
  }

  addText(str: string): Cell {
    let cell = new Cell(this, this.curCol, this.curRow);
    cell.add(new PIXI.Text(str));
    this.cells[this.curRow].push(cell);
    this.curCol++;
    this.addChild(cell);
    return cell;
  }

  row(): Table {
    if (this.cells.length > 1 && !this.checkRows()) {
      throw new Error('Не одинаковое количество колонок');
    }
    this.curRow++;
    this.curCol = 0;
    this.cells.push([]);
    return this;
  }

  private checkRows(): boolean {
    return this.cells.map(cr => cr.map(c => c.span).reduce((a, b) => a + b))
      .every((x, i, a) => x === a[0]);
  }

  colspan(i: number) {
    this.curCol += i - 1;
  }

  build() {
    let curX: number = 0;
    let curY: number = 0;

    for (let row: number = 0; row < this.cells.length; row++) {
      for (let col: number = 0; col < this.cells[row].length; col++) {
        var cell = this.cells[row][col];

        let maxHeight = this.getRowHeight(row);
        // let maxWidth = this.getColumnWidth(col);
        let maxWidth = this.getColumnWidthWithSpan(cell.columnIndex, cell.span);

        cell.setSize(maxWidth, maxHeight);
        cell.position.set(curX, curY);
        curX += cell.width;
      }
      curX = 0;
      curY += cell.height;
    }
  }

  build2(drawLines: boolean = false) {
    let curX: number = 0;
    let curY: number = 0;

    let allCells = this.cells.reduce((a, b) => a.concat(b));

    let numberOfCols = this.columnsCount;
    let colWidths = [];
    for (let i = 0; i < numberOfCols; i++) {
      colWidths.push(this.getColumnWidth(i));
    }
    let rowHeights = [];
    for (let i = 0; i < this.cells.length; i++) {
      rowHeights.push(this.getRowHeight(i));
    }

    allCells.forEach(cell => {
      let width = 0;
      for (let i = cell.columnIndex; i < cell.columnIndex + cell.span; i++) {
        width += colWidths[i];
      }
      cell.setSize(width, rowHeights[cell.rowIndex]);
    });

    for (let row: number = 0; row < this.cells.length; row++) {
      for (let col: number = 0; col < this.cells[row].length; col++) {
        var cell = this.cells[row][col];
        cell.position.set(curX, curY);
        curX += cell.width;
      }
      curX = 0;
      curY += cell.height;
    }

    if (drawLines) {
      this.cells.reduce((a, b) => a.concat(b)).forEach((x, i, a) => {
        if (i !== a.length - 1) x.drawLines();
      });

    }

  }

  private getRowHeight(row: number): number {
    return this.cells[row].map(x => x.height)
      .reduce((a, b) => Math.max(a, b));
  }

  private getColumnWidthWithSpan(col: number, span: number): number {
    let result = 0;
    for (let i = 0; i < span; i++) {
      result += this.getColumnWidth(col + i);
    }
    return result;
  }

  private getColumnWidth(col: number): number {
    let widths = [];

    this.cells.reduce((a, b) => a.concat(b))
      .filter(x => x.columnIndex === col)
      .forEach(c => {
        // if (c.span === 1) {
        let result = c.width;
        widths.push(result);
        // }
      });

    return widths.reduce((a, b) => Math.max(a, b));
  }

  debug(debug: boolean) {
    if (debug) this.cells.forEach(r => r.forEach(c => c.debug(debug)));
  }
}



