import InteractionManager = PIXI.interaction.InteractionManager;
import {ElementRef} from "@angular/core";
import InteractionEvent = PIXI.interaction.InteractionEvent;
import {Subject} from "rxjs/Subject";
import {Observable} from "rxjs/Observable";
import 'rxjs/add/operator/buffer';
import 'rxjs/add/operator/debounce';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/filter';
import 'rxjs/add/observable/timer';

export class MyInput {
  private keymap = new Map();
  private _leftMouseDown: boolean;
  private _rightMouseDown: boolean;
  private _mousePoint: PIXI.Point = new PIXI.Point(0, 0);
  private _lastDragMousePos: PIXI.Point;
  private _zoomSubject = new Subject();
  private _rightDragSubject = new Subject();
  private _mouseDownSubject = new Subject();
  private _mouseUpSubject = new Subject();
  private _mouseUpOutsideSubject = new Subject();
  private _mouseMoveSubject = new Subject();
  private _mouseOutSubject = new Subject();
  private _keyDownSubject = new Subject();

  public onZoom: Observable<number> = this._zoomSubject.asObservable();
  public onRightDrag: Observable<any> = this._rightDragSubject.asObservable();
  public onMouseDown: Observable<InteractionEvent> = this._mouseDownSubject.asObservable();
  public onMouseUp: Observable<InteractionEvent> = this._mouseUpSubject.asObservable();
  public onMouseUpOutside: Observable<InteractionEvent> = this._mouseUpOutsideSubject.asObservable();
  public onMouseMove: Observable<InteractionEvent> = this._mouseMoveSubject.asObservable();
  public onMouseOut: Observable<InteractionEvent> = this._mouseOutSubject.asObservable();
  public onKeyDown: Observable<KeyboardEvent> = this._keyDownSubject.asObservable();

  public mousePosition: PIXI.Point = new PIXI.Point();
  public onMouseDoubleClick: Observable<any>;

  constructor(private _interactionManager: InteractionManager, pixiContainer: ElementRef) {
    _interactionManager.on('mousemove', (e: InteractionEvent) => this.handleMousemove(e));
    _interactionManager.on('mousedown', (e: InteractionEvent) => this.handleMousedown(e));
    _interactionManager.on('mouseup', (e: InteractionEvent) => this.handleMouseup(e));
    _interactionManager.on('mouseout', (e: InteractionEvent) => this.handleMouseout(e));
    _interactionManager.on('mouseupoutside', (e: InteractionEvent) => this.handleMouseupOutside(e));
    _interactionManager.on('rightdown', (e: InteractionEvent) => this.handleRightMousedown(e));
    _interactionManager.on('rightup', (e: InteractionEvent) => this.handleRightMouseup(e));

    this.onMouseDoubleClick = this._mouseDownSubject
      .buffer(this._mouseDownSubject.debounce(x => Observable.timer(250)))
      .map(list => list.length)
      .filter(x => x === 2);

    let element: HTMLElement = pixiContainer.nativeElement;
    document.addEventListener('keydown', (e: KeyboardEvent) => this.handleKeydown(e), false);
    document.addEventListener('keyup', (e: KeyboardEvent) => this.handleKeyup(e), false);
    element.addEventListener('wheel', (e: WheelEvent) => this.handleMouseWheel(e), false);
    element.addEventListener('contextmenu', (e) => {
      e.preventDefault();
    }, false);
  }

  private handleMousemove(e: InteractionEvent) {
    this._mousePoint = e.data.global;
    if (this._lastDragMousePos && this._rightMouseDown) {
      this._rightDragSubject.next({
        dx: e.data.global.x - this._lastDragMousePos.x,
        dy: e.data.global.y - this._lastDragMousePos.y
      });
      this._lastDragMousePos = e.data.global.clone();
    }
    this.mousePosition.set(e.data.global.x, e.data.global.y);
    this._mouseMoveSubject.next(e);
  }

  private handleMousedown(e: InteractionEvent) {
    this._leftMouseDown = true;
    this._mouseDownSubject.next(e);
  }

  private handleMouseup(e: InteractionEvent) {
    this._leftMouseDown = false;
    this._mouseUpSubject.next(e);
  }

  private handleKeydown(e: KeyboardEvent) {
    this.keymap[e.keyCode] = true;
    this._keyDownSubject.next(e);
  }

  private handleKeyup(e: KeyboardEvent) {
    this.keymap[e.keyCode] = false;
  }

  private handleMouseWheel(e: WheelEvent) {
    if (e.deltaY < 0) {
      this._zoomSubject.next(0.1);
    } else if (e.deltaY > 0) {
      this._zoomSubject.next(-0.1);
    }
    e.preventDefault();
  }

  private handleRightMousedown(e: InteractionEvent) {
    this._rightMouseDown = true;
    this._lastDragMousePos = e.data.global.clone();
  }

  private handleRightMouseup(e: InteractionEvent) {
    this._rightMouseDown = false;
    this._lastDragMousePos = undefined;
  }

  public isCtrlKeyDown(): boolean {
    return this.keymap[17] === true;
  }

  public isLeftMouseDown(): boolean {
    return this._leftMouseDown === true;
  }

  public isRightMouseDown(): boolean {
    return this._rightMouseDown === true;
  }

  public getMousePoint(): PIXI.Point {
    return this._mousePoint;
  }

  private handleMouseupOutside(e: InteractionEvent) {
    this._mouseUpOutsideSubject.next(e);
  }

  private handleMouseout(e: InteractionEvent) {
    this._mouseOutSubject.next(e);
  }
}



