import {Component, EventEmitter, HostListener, NgZone, OnDestroy, OnInit, Output, ViewChild} from '@angular/core';
import {Zoom} from "./zoom";
import {MyInput} from "./input";
import Matrix = PIXI.Matrix;
import {SimpleBlock} from "./objects/simple-block";
import {Port} from "./objects/port";
import {Connection} from "./objects/connection";
import {EditorObject} from "./objects/editor-object";
import {Condition} from "./objects/condition";
import {PhantomConnection} from "./objects/phantom-connection";
import {Aligner} from "./objects/aligner";
import {SelectionFrame} from "./objects/selection-frame";
import {SelectorFn} from "./selector-fn";
import {Bump} from "../../ext/bump";
import WebGLRenderer = PIXI.WebGLRenderer;
import InteractionEvent = PIXI.interaction.InteractionEvent;
import {getLoader} from "./get-loader";
import {extractImage} from "./extract-image";
import * as _ from "lodash";

@Component({
  selector: 'app-editor2',
  templateUrl: './editor2.component.html',
  styleUrls: ['./editor2.component.scss']
})
export class Editor2Component implements OnInit, OnDestroy {
  @ViewChild('pixi') pixiContainer;
  aligner: Aligner = new Aligner();
  @Output() select: EventEmitter<any[]> = new EventEmitter();
  private renderer: PIXI.WebGLRenderer;
  private game: PIXI.Container = new PIXI.Container();
  private connectionContainer: PIXI.Container = new PIXI.Container();
  private zoomContainer: PIXI.Container = new PIXI.Container();
  private zoom: Zoom = new Zoom();
  private overlay: PIXI.Container = new PIXI.Container();
  private selectedPort: Port;
  private stage: PIXI.Container = new PIXI.Container();
  private selectedObjects: EditorObject[] = [];
  private selectionFrame: SelectionFrame;
  private phantomConnection: PhantomConnection = new PhantomConnection();
  private bump: Bump = new Bump(PIXI);
  private dragObjects: { object: EditorObject; offset: PIXI.Point }[] = [];

  constructor(private _ngZone: NgZone) {
  }

  public ngOnInit() {
    this._ngZone.runOutsideAngular(() => this.init());
  }

  public ngOnDestroy(): void {
    PIXI.ticker.shared.destroy();
    if (this.renderer) this.renderer.destroy();
  }

  @HostListener('window:resize')
  onResize(event) {
    if (!this.renderer) return;
    const el = this.pixiContainer.nativeElement.parentElement;
    const w = el.clientWidth;
    const h = el.clientHeight;
    const renderer = this.renderer;
    renderer.view.style.width = w + "px";
    renderer.view.style.height = h + "px";
    renderer.resize(w, h);
  }

  public createBlock() {
    const b = new SimpleBlock();
    const p = new PIXI.Point(this.renderer.width / 2 - b.width, this.renderer.height / 2 - b.height);
    b.setPosition(this.game.toLocal(p));
    this.game.addChild(b);
  }

  public createCondition() {
    const con = new Condition(3, 'Условие');
    const p = new PIXI.Point(this.renderer.width / 2 - con.width, this.renderer.height / 2 - con.height);
    con.setPosition(this.game.toLocal(p));
    this.game.addChild(con);
  }

  public autoLayout() {
    this.aligner.autoAlign(this.game.children.filter(x => x instanceof EditorObject) as EditorObject[]);
    this.zoomToFit();
  }

  public exportImage() {
    this.updateZoom(new PIXI.Matrix());
    extractImage(this.renderer, this.zoomContainer).then(() => this.updateZoom());
  }

  public zoomToFit() {
    const zoomOffset = 0.85;
    const xOffset = -150;
    const f = () => {
      const bounds: PIXI.Rectangle = this.game.getLocalBounds();
      const realBounds = {x: bounds.x, y: bounds.y, width: bounds.width, height: bounds.height};
      const desiredScale = Math.min(this.renderer.width / realBounds.width, this.renderer.height / realBounds.height) * zoomOffset;
      const bCenter = new PIXI.Point(bounds.x + bounds.width / 2, bounds.y + bounds.height / 2);
      const zoomCenter = this.game.toGlobal(bCenter);
      this.zoom.setScaleCenter(zoomCenter.x, zoomCenter.y);
      this.zoom.currentScale = desiredScale;
      this.zoom.translateBy(this.renderer.width / 2 - zoomCenter.x, this.renderer.height / 2 - zoomCenter.y);
      this.updateZoom();
    };
    f();
    f();
    this.zoom.translateBy(xOffset, 0);
    this.updateZoom();
  }

  public deleteSelected() {
    this.selectedObjects.forEach(o => {
      if (o.hasConnenctions()) {
        o.children.filter(x => x instanceof Port)
          .map(x => x as Port)
          .forEach(x => x.connections.forEach(c => {
            c.parent.removeChild(c);
            c.clearPorts();
          }));
      }
      this.game.removeChild(o);
    });
    this.selectedObjects = [];
  }

  private init() {
    const renderer: PIXI.WebGLRenderer = this.renderer = PIXI.autoDetectRenderer({
      view: this.pixiContainer.nativeElement,
      width: this.pixiContainer.nativeElement.parentElement.clientWidth,
      height: this.pixiContainer.nativeElement.parentElement.clientHeight,
      transparent: true,
      autoResize: true,
      antialias: true,
    }) as WebGLRenderer;
    let time = new Date().getTime();
    getLoader().onComplete.add((() => {
      this.createGame(this.game);
      this.zoomContainer.addChild(this.connectionContainer);
      this.zoomContainer.addChild(this.game);
      this.stage.addChild(this.zoomContainer);
      this.stage.addChild(this.overlay);
      console.log('TIME - ' + (new Date().getTime() - time));
    }));
    const input = new MyInput(renderer.plugins.interaction, this.pixiContainer);
    this.setupInput(input);
    PIXI.ticker.shared.add((deltaTime) => {
      const mousePoint = this.game.toLocal(input.getMousePoint(), this.overlay);
      this.zoom.setScaleCenter(mousePoint.x, mousePoint.y);
      renderer.render(this.stage);
    });
  }

  private startDrag(e: InteractionEvent) {
    this.dragObjects = this.selectedObjects.map(o => {
      const globalPos = o.getGlobalPosition();
      return {
        object: o,
        offset: new PIXI.Point(e.data.global.x - globalPos.x, e.data.global.y - globalPos.y)
      };
    });
  }

  private updateZoom(_t: Matrix = null) {
    const t: Matrix = _t || this.zoom.Transform;
    this.connectionContainer.transform.localTransform.set(t.a, t.b, t.c, t.d, t.tx, t.ty);
    this.game.transform.localTransform.set(t.a, t.b, t.c, t.d, t.tx, t.ty);
    this.zoomContainer.transform.updateTransform(this.game.transform);
  }

  private createConnection(p1: Port, p2: Port) {
    if (p1.parent === p2.parent) return;
    if (p1.isInput === p2.isInput) return;
    this.connectionContainer.addChild(new Connection(p1, p2));
  }

  private createGame(game: PIXI.Container) {
    const nodes = [];
    for (let i = 0; i < 10; i++) {
      const t = new SimpleBlock();
      t.blockName = 'adfads';
      t.blockCode = 'sdfg';
      t.blockRole = 'dfasf';
      t.blockHours = '1232';

      t.setPosition(new PIXI.Point(Math.random() * 1000 - 500, Math.random() * 1000 - 500));
      nodes.push(t);
      game.addChild(t);
      if (i > 1) {
        const c = new Connection(t.inPort, nodes[i - 1].outPort);
        this.connectionContainer.addChild(c);
      }
    }
    const c1 = new Condition(3, 'Условие');
    c1.setPosition(new PIXI.Point(500, 500));
    game.addChild(this.phantomConnection);
    game.addChild(c1);
  }

  private notifySelect(selectedObjects: EditorObject[]) {
    this._ngZone.run(() => {
      this.select.emit(this.selectedObjects.map(selectedObject => 'objekt-Objekt'));
    });
  }

  private startSelectionFrame(mousePos: PIXI.Point) {
    if (this.selectionFrame) this.stopSelectionFrame();
    this.selectionFrame = new SelectionFrame(mousePos.clone());
    this.overlay.addChild(this.selectionFrame);
  }

  private stopSelectionFrame() {
    this.overlay.removeChild(this.selectionFrame);
    this.selectionFrame = null;
  }

  private updateSelectionFrame(mousePos: PIXI.Point) {
    if (!this.selectionFrame) return;
    this.selectionFrame.update(mousePos);
  }

  private getFrameSelection(rect: PIXI.Rectangle, direction: { down: boolean; right: boolean }): EditorObject[] {
    return this.game.children.filter(direction.right ?
      SelectorFn.containsSelectorFn(this.bump, rect) : SelectorFn.intersectSelectorFn(this.bump, rect)) as EditorObject[];
  }

  private selectAll() {
    this.setSelectedObjects(this.game.children.filter(x => x instanceof EditorObject) as EditorObject[], true);
  }

  private deselectAll() {
    this.setSelectedObjects([], false);
  }

  private setSelectedObjects(_objects: EditorObject | EditorObject[], select = true, append = false) {
    const objects = _objects instanceof Array ? _objects : [_objects];
    if (append) {
      this.selectedObjects = this.selectedObjects.concat(objects);
    } else {
      this.selectedObjects.forEach(o => o.setSelected(false));
      this.selectedObjects = this.select ? objects : [];
    }
    objects.forEach(o => o.setSelected(select));
    this.notifySelect(this.selectedObjects);
  }

  private stopDrag() {
    this.dragObjects = [];
  }

  private updateDrag(e: PIXI.interaction.InteractionEvent) {
    this.dragObjects.forEach(dragObject => {
      const newPos = dragObject.object.parent.toLocal(new PIXI.Point(e.data.global.x - dragObject.offset.x, e.data.global.y - dragObject.offset.y));
      dragObject.object.setPosition(newPos);
    });
  }

  private setupInput(input: MyInput) {
    input.onZoom.subscribe(zoom => {
      this.zoom.zoomBy(zoom, zoom);
      this.updateZoom();
    });
    input.onRightDrag.subscribe(d => {
      this.zoom.translateBy(d.dx, d.dy);
      this.updateZoom();
    });
    input.onKeyDown.subscribe(e => {
      switch (e.keyCode) {
        case 46:
          this.deleteSelected();
          break;
        case 65:
          if (input.isCtrlKeyDown()) {
            this.selectAll();
            e.preventDefault();
          }
          break;
      }
    });
    input.onMouseMove.subscribe(e => {
      if (this.selectedPort != null) {
        const hitObject = this.renderer.plugins.interaction.hitTest(e.data.global, this.stage);
        if (hitObject instanceof Port && hitObject.isInput === this.selectedPort.isInput) {
          // нельзя так соединять порты!
        }
        const portSnap = this.snapToPort(e.data.global);
        this.phantomConnection.update(portSnap.position);
      }
      if (input.isLeftMouseDown()) {
        this.updateDrag(e);
        this.updateSelectionFrame(e.data.global);
      } else {
        this.stopDrag();
        this.stopSelectionFrame();
      }
    });
    input.onMouseOut.merge(input.onMouseUpOutside).subscribe(() => {
      this.stopSelectionFrame();
    });
    input.onMouseUp.subscribe(e => {
      this.stopDrag();
      if (this.selectionFrame) {
        const rect = this.selectionFrame.getRect();
        if (rect) this.setSelectedObjects(this.getFrameSelection(rect, this.selectionFrame.direction), true);
        this.stopSelectionFrame();
      }
    });
    input.onMouseDown.subscribe(e => {
      const hitObject = this.renderer.plugins.interaction.hitTest(e.data.global, this.stage);
      if (this.phantomConnection.isActive()) {
        const port = this.snapToPort(e.data.global).port;
        if (port) this.createConnection(this.selectedPort, port);
        this.selectedPort = null;
        this.phantomConnection.hide();
        return;
      }
      if (hitObject instanceof Port) {
        if (!this.selectedPort || this.selectedPort === hitObject) {
          this.selectedPort = hitObject;
          this.phantomConnection.setPort(this.selectedPort);
          return;
        }
        this.createConnection(this.selectedPort, hitObject as Port);
        this.selectedPort = null;
        this.phantomConnection.hide();
      } else if (hitObject instanceof EditorObject) {
        if (!hitObject.selected) this.setSelectedObjects(hitObject, true, input.isCtrlKeyDown());
        this.startDrag(e);
      } else if (hitObject === null) {
        this.deselectAll();
        if (this.selectedPort !== null) {
          this.selectedPort = null;
          this.phantomConnection.hide();
        }
        this.startSelectionFrame(e.data.global);
      }
    });
    input.onMouseDoubleClick.subscribe(e => {
      alert('DOUBLE CLICK!!!11');
    });
  }

  private snapToPort(mousePos: PIXI.Point): { position: PIXI.Point, port: Port } {
    const distanceThreshold = 100;
    const ports = _.sortBy(_.flatMap(this.game.children, o => o instanceof EditorObject ? o.getPorts() : [])
      .map(p => {
        const portPos = p.getGlobalPosition();
        return {
          port: p,
          distance: Math.sqrt(Math.pow(portPos.x - mousePos.x, 2) + Math.pow(portPos.y - mousePos.y, 2))
        };
      }).filter(o => o.distance < distanceThreshold && o.port !== this.selectedPort && o.port.isInput !== this.selectedPort.isInput), 'distance');
    return ports.length > 0 ? {position: ports[0].port.getGlobalPosition(), port: ports[0].port} : {
      position: mousePos,
      port: null
    };
  }
}



