import * as d3 from 'd3';
import {TreeLayout} from "d3-hierarchy";
import {Subject} from "rxjs/Subject";
import {Observable} from "rxjs/Observable";

export class CollapsibleTree {
  public duration = 300;
  public margin = {top: 20, right: 120, bottom: 20, left: 120};
  private treemap: TreeLayout<any>;
  private svg;
  private xOffset = 100;
  private i = 1;
  private root;

  private _navigate: Subject<any> = new Subject();
  public onNavigate = this._navigate.asObservable();

  public init(svgElement, treeData) {
    let margin = this.margin;
    let width = svgElement.clientWidth - margin.right - margin.left;
    let height = svgElement.clientHeight - margin.top - margin.bottom;
    this.svg = d3.select(svgElement)
      .attr("width", width + margin.right + margin.left)
      .attr("height", height + margin.top + margin.bottom)
      .append("g")
      .attr("transform", "translate(" + margin.left + "," + margin.top + ")");
    this.treemap = d3.tree().size([height, width]);
    let root = d3.hierarchy(treeData, (d: any) => d.children);
    (<any>root).x0 = height / 2;
    (<any>root).y0 = 0;
    root.children.forEach(x => this.collapse(x));
    this.root = root;
    this.update(root);
  }

  private collapse(d) {
    if (d.children) {
      d._children = d.children;
      d._children.forEach(x => this.collapse(x));
      d.children = null;
    }
  }

  private click(d) {
    if (d.children) {
      d._children = d.children;
      d.children = null;
    } else {
      d.children = d._children;
      d._children = null;
    }
    this.update(d);
  }

  private static diagonal(s, d) {
    return `M ${s.y} ${s.x} C ${(s.y + d.y) / 2} ${s.x},${(s.y + d.y) / 2} ${d.x},${d.y} ${d.x}`
  }

  public update(source) {
    let treeData = this.treemap(this.root);
    let nodes = treeData.descendants(),
      links = treeData.descendants().slice(1);
    nodes.forEach(d => {
      d.y = d.depth * this.xOffset
    });
    let node = this.svg.selectAll('g.node').data(nodes, d => d.id || (d.id = this.getId()));

    let nodeEnter = node.enter().append('g')
      .attr('class', 'node')
      .attr("transform", d => "translate(" + source.y0 + "," + source.x0 + ")")
      .on('contextmenu', d => {
        d3.event.preventDefault();
        this._navigate.next(d.data);
      })
      .on('click', d => this.click(d));
    nodeEnter.append('circle')
      .attr('class', 'node')
      .attr('r', 1e-6)
      .style("fill", (d: any) => d._children ? "lightsteelblue" : "#fff");
    nodeEnter.append('text')
      .attr("dy", ".35em")
      .attr("x", (d: any) => d.children || d._children ? -13 : 13)
      .attr("text-anchor", (d: any) => d.children || d._children ? "end" : "start")
      .text(d => d.data.name);
    let nodeUpdate = nodeEnter.merge(node);
    nodeUpdate.transition()
      .duration(this.duration)
      .attr("transform", (d) => {
        return "translate(" + d.y + "," + d.x + ")";
      });
    nodeUpdate.select('circle.node')
      .attr('r', 10)
      .style("fill", (d: any) => {
        return d._children ? "lightsteelblue" : "#fff";
      })
      .attr('cursor', 'pointer');
    let nodeExit = node.exit().transition()
      .duration(this.duration)
      .attr("transform", (d) => {
        return "translate(" + source.y + "," + source.x + ")";
      })
      .remove();
    nodeExit.select('circle')
      .attr('r', 1e-6);
    nodeExit.select('text')
      .style('fill-opacity', 1e-6);

    let link = this.svg.selectAll('path.link')
      .data(links, d => d.id);
    let linkEnter = link.enter().insert('path', "g")
      .attr("class", "link")
      .attr('d', d => {
        let o = {x: source.x0, y: source.y0};
        return CollapsibleTree.diagonal(o, o);
      });
    let linkUpdate = linkEnter.merge(link);
    linkUpdate.transition()
      .duration(this.duration)
      .attr('d', d => {
        return CollapsibleTree.diagonal(d, d.parent);
      });
    let linkExit = link.exit().transition()
      .duration(this.duration)
      .attr('d', d => {
        let o = {x: source.x, y: source.y};
        return CollapsibleTree.diagonal(o, o);
      })
      .remove();
    nodes.forEach((d: any) => {
      d.x0 = d.x;
      d.y0 = d.y;
    });
  }

  private getId() {
    this.i += 1;
    return this.i;
  }


}



