import {EditorObject} from "./editor-object";
import {Port, PortOrientation} from "./port";

export class Condition extends EditorObject {
  private _box: PIXI.Graphics;

  public inPort: Port;
  public outPorts: Port[] = [];

  constructor(private rowCount: number, private title: string) {
    super();
    this.interactive = true;
    this.createView();
  }

  public getPorts(): Port[] {
    return this.outPorts.concat([this.inPort]);
  }

  protected createView() {
    let box = this._box = new PIXI.Graphics();
    let headerHeight = 50;
    let rowHeight = 40;
    let height = headerHeight + rowHeight * this.rowCount;
    let width = 200;
    box.beginFill(0xffe311);
    box.lineStyle(1);
    box.drawRoundedRect(0, 0, width, height, 10);
    box.endFill();

    let nameTxt = new PIXI.Text(this.title);
    nameTxt.x = 15;
    nameTxt.y = 5;

    let inPort = this.inPort = new Port(this, PortOrientation.Left);
    inPort.x = 0;
    inPort.y = headerHeight / 2;
    inPort.isInput = true;

    let answerLabels = [];

    for (let i = 1; i <= this.rowCount; i++) {
      let port = new Port(this, PortOrientation.Right);
      port.x = width;
      port.y = headerHeight + i * rowHeight - rowHeight / 2;
      this.outPorts.push(port);

      let label = new PIXI.Text('Otvet ' + i);
      label.x = 25;
      label.y = headerHeight + (i - 1) * rowHeight;
      answerLabels.push(label);
    }

    this.addChild(box, nameTxt, inPort, ...this.outPorts, ...answerLabels);
  }
}



