import Container = PIXI.Container;
import {Table} from "./table";
import {Alignment} from "./alignment.enum";
import Graphics = PIXI.Graphics;

export class Cell extends Container {
  private table: Table;
  private curObj: Container;
  private curAlignment: Alignment = Alignment.TopLeft;
  private lines: Graphics = new Graphics();

  columnIndex: number = 0;
  rowIndex: number = 0;

  private padding: number = 0;

  span: number = 1;
  private size: PIXI.Sprite;

  private isDebug: boolean = false;
  private debugRect = new PIXI.Graphics();

  constructor(table: Table, col: number, row: number, debug: boolean = false) {
    super();

    this.isDebug = debug;
    this.table = table;
    this.columnIndex = col;
    this.rowIndex = row;
    if (this.isDebug) this.drawDebug();
  }


  add(obj: Container): Cell {
    this.removeChild(this.curObj);
    this.curObj = obj;
    this.addChild(obj);
    return this;
  }

  //region Align
  align(align: Alignment): Cell {
    switch (align) {
      case Alignment.TopLeft:
        this.alignTopLeft();
        break;
      case Alignment.Top:
        this.alignTop();
        break;
      case Alignment.TopRight:
        this.alignTopRight();
        break;
      case Alignment.BottomLeft:
        this.alignBottomLeft();
        break;
      case Alignment.Bottom:
        this.alignBottom();
        break;
      case Alignment.BottomRight:
        this.alignBottomRight();
        break;
      case Alignment.CenterLeft:
        this.alignCenterLeft();
        break;
      case Alignment.Center:
        this.alignCenter();
        break;
      case Alignment.CenterRight:
        this.alignCenterRight();
        break;
    }
    return this;
  }

  alignTopLeft(): Cell {
    this.curAlignment = Alignment.TopLeft;
    this.curObj.x = this.padding;
    this.curObj.y = this.padding;
    return this;
  }

  alignTop(): Cell {
    this.curAlignment = Alignment.Top;
    this.curObj.x = (this.width - this.curObj.width) / 2;
    this.curObj.y = this.padding;
    return this;
  }

  alignTopRight(): Cell {
    this.curAlignment = Alignment.TopRight;
    this.curObj.x = this.width - this.curObj.width - this.padding;
    this.curObj.y = this.padding;
    return this;
  }

  alignBottomLeft(): Cell {
    this.curAlignment = Alignment.BottomLeft;
    this.curObj.x = this.padding;
    this.curObj.y = this.height - this.curObj.height - this.padding;
    return this;
  }

  alignBottom(): Cell {
    this.curAlignment = Alignment.Bottom;
    this.curObj.x = (this.width - this.curObj.width) / 2;
    this.curObj.y = this.height - this.curObj.height - this.padding;
    return this;
  }

  alignBottomRight(): Cell {
    this.curAlignment = Alignment.BottomRight;
    this.curObj.x = this.width - this.curObj.width - this.padding;
    this.curObj.y = this.height - this.curObj.height - this.padding;
    return this;
  }

  alignCenterLeft(): Cell {
    this.curAlignment = Alignment.CenterLeft;
    this.curObj.x = this.padding;
    this.curObj.y = (this.height - this.curObj.height) / 2;
    return this;
  }

  alignCenter(): Cell {
    this.curAlignment = Alignment.Center;
    this.curObj.x = (this.width - this.curObj.width) / 2;
    this.curObj.y = (this.height - this.curObj.height) / 2;
    return this;
  }

  alignCenterRight(): Cell {
    this.curAlignment = Alignment.CenterRight;
    this.curObj.x = this.width - this.curObj.width - this.padding;
    this.curObj.y = (this.height - this.curObj.height) / 2;
    return this;
  }


  //endregion

  //region Padding
  pad(pad: number) {
    this.padding = pad;
    this.align(this.curAlignment);
  }

  //endregion

  colspan(i: number): Cell {
    this.span = i;
    this.table.colspan(i);
    return this;
  }

  setSize(width: number, height: number) {
    const smallTexture = PIXI.RenderTexture.create(width, height);
    this.size = new PIXI.Sprite(smallTexture);

    this.children.forEach(x => this.removeChild(x));
    this.addChild(this.size);
    if (this.isDebug) this.drawDebug();
    this.add(this.curObj);
    this.update();
  }

  update() {
    this.align(this.curAlignment);
    this.pad(this.padding);
    //colspan
  }

  private drawDebug() {
    this.debugRect.lineStyle(1, 0xFF0000);
    this.debugRect.drawRect(0, 0, this.width, this.height);
    this.addChild(this.debugRect);
  }

  debug(debug: boolean) {
    this.isDebug = debug;
    this.children.forEach(x => this.removeChild(x));
    this.addChild(this.size);
    if (debug) this.drawDebug();
    this.add(this.curObj);
    if (this.curObj instanceof Table) this.curObj.debug(debug);
  }

  drawLines() {
    this.lines.clear();
    this.lines.lineStyle(1);
    if (this.rowIndex !== this.table.rowsCount - 1) {
      this.lines.moveTo(0, this.height);
      this.lines.lineTo(this.width, this.height);
    } else this.lines.moveTo(this.width, this.height);
    if (this.span !== this.table.columnsCount) this.lines.lineTo(this.width, 0);
    this.addChild(this.lines);
  }
}



