import Sprite = PIXI.Sprite;
export class Button extends PIXI.Container {
  private sprite: Sprite;
  private s: number = 1;

  private down = delta => this.scaleDown(delta);
  private up = delta => this.scaleUp(delta);

  constructor(name: string, click: Function) {
    super();
    this.sprite = new Sprite(PIXI.loader.resources[name].texture);
    this.sprite.width = 32;
    this.sprite.height = 32;
    this.sprite.interactive = true;

    this.sprite.on('pointerout', e => {
      PIXI.ticker.shared.remove(this.up);
      PIXI.ticker.shared.add(this.down);
    });
    this.sprite.on('pointerover', e => {
      PIXI.ticker.shared.remove(this.down);
      PIXI.ticker.shared.add(this.up);
    });

    this.addChild(this.sprite);
    this.sprite.on('click', (e) => click());
  }

  scaleUp(delta: number) {
    this.s += 0.1 * delta;
    this.sprite.scale.set(this.s, this.s);
    if (this.s >= 1.5) {
      PIXI.ticker.shared.remove(this.up);
    }
  }

  scaleDown(delta: number) {
    this.s -= 0.2 * delta;
    this.sprite.scale.set(this.s, this.s);
    if (this.s <= 1) {
      this.sprite.scale.set(1, 1);
      PIXI.ticker.shared.remove(this.down);
    }
  }
}



