import {EditorObject} from "./editor-object";
import InteractionEvent = PIXI.interaction.InteractionEvent;

export class GroupObject extends EditorObject {
  private container: PIXI.Container;
  private frame: PIXI.Graphics;

  private getframeColor() {
    return this._selected ? 0xCC0000 : 0xCCFF00
  };

  constructor() {
    super();
    this.createDragAndDropFor(this);
  }

  public getChildren: () => EditorObject[] = () => this.container.children as EditorObject[];

  protected createView() {
    this.container = new PIXI.Container();
    this.addChild(this.container);
    this.updateFrame();
  }

  public add(object: EditorObject) {
    this.container.addChild(object);
    object.onMove.subscribe(() => this.updateFrame());
    this.updateFrame();
  }

  private updateFrame() {
    if (!this.frame) {
      this.frame = new PIXI.Graphics();
      this.addChild(this.frame);
    }
    this.frame.clear();
    this.frame.beginFill(0x777777, 0.5);
    this.frame.lineStyle(2, this.getframeColor());
    let bounds = this.container.getLocalBounds();
    this.frame.drawRect(bounds.x, bounds.y, bounds.width, bounds.height);
    this.frame.endFill();
  }

  protected updateSelectionFrame(isSelected) {
    this.updateFrame();
    if (isSelected && !this._selected) {

    } else {

    }
  }

  public updatePosition(e: InteractionEvent) {
    this.position.x = e.data.global.x - this._dragOffset.x;
    this.position.y = e.data.global.y - this._dragOffset.y;
    this._moveSubject.next(this.position);
    this.container.children.forEach((x: EditorObject) => x.moved());
  }
}



