import {Component, OnInit} from '@angular/core';
import {FormArray, FormBuilder, FormControl, FormGroup} from "@angular/forms";
import {Level} from "./level";

@Component({
  selector: 'app-level-editor',
  templateUrl: './level-editor.component.html',
  styleUrls: ['./level-editor.component.scss']
})
export class LevelEditorComponent implements OnInit {
  form: FormGroup;

  constructor(private _formBuilder: FormBuilder) {
    this.form = _formBuilder.group({
      levels: _formBuilder.array([
        _formBuilder.group(this.makeLevel())
      ])
    });
  }

  ngOnInit() {
  }

  onSubmit(o) {
    console.log(o);
  }

  makeLevel() {
    return {
      name: '',
      code: '',
      autoNumbering: true,
      color: 'ff0000'
    };
  }

  addLevel() {
    const control = <FormArray>this.form.controls.levels;
    const level = this._formBuilder.group(this.makeLevel());
    control.push(level);
  }

  removeLevel(level, i) {
    const control = <FormArray>this.form.controls.levels;
    control.removeAt(i);
  }
}



