import {EditorObject} from "./editor-object";
import {Connection} from "./connection";

export class Port extends PIXI.Graphics {
  isInput: boolean = false;
  connections: Connection[] = [];
  private orientation;
  private _color = 0x00aa33;
  get color(): number {
    return this._color;
  }

  set color(c: number) {
    this._color = c;
    this.update();
  }

  constructor(public parent: EditorObject, orientation = PortOrientation.Left) {
    super();
    this.orientation = orientation;
    this.update();
    this.interactive = true;
    this.buttonMode = true;
  }

  private update() {
    this.beginFill(this.color);
    this.lineStyle(2);
    const angle = this.getAngle(this.orientation);
    this.arc(0, 0, 10, angle, Math.PI + angle);
    this.endFill();
  }

  private getAngle(orientation: PortOrientation) {
    switch (orientation) {
      case PortOrientation.Left:
        return Math.PI / 2;
      case PortOrientation.Right:
        return Math.PI / 2 + Math.PI;
      default:
        return Math.PI / 2;
    }
  }
}

export enum PortOrientation {
  Left,
  Right
}



