import WebGLRenderer = PIXI.WebGLRenderer;
import {MyExtract} from "./my-extract";
import Container = PIXI.Container;

export function extractImage(renderer: WebGLRenderer, container: Container): Promise<any> {
  const extract = new MyExtract(renderer);
  return new Promise((resolve, reject) => {
    extract.canvas(container, new PIXI.Rectangle(0, 0, Math.round(container.width), Math.round(container.height))).toBlob((blob) => {
      window.open(URL.createObjectURL(blob));
      resolve();
    }, 'image/png');
  });
}



