import {Component, HostListener, Input, NgZone, OnDestroy, OnInit, ViewChild} from '@angular/core';
import * as PIXI from 'pixi.js';
import {EditorObject} from "./objects/editor-object";
import {Observable} from "rxjs/Observable";
import {ViewSet} from "./objects/view-set";
import {Connection} from "./objects/connection";
import {KeyboardKey} from "./objects/keyboard";
import {Bump} from "../../ext/bump";
import {ViewGroup} from "./objects/view-group";
import {GroupObject} from "./objects/group-object";
import {Zoom} from "./zoom";
import {InteractionInput} from "./input";


@Component({
  selector: 'app-editor',
  templateUrl: './editor.component.html',
  styleUrls: ['./editor.component.scss']
})
export class EditorComponent implements OnInit, OnDestroy {
  @ViewChild('pixi') pixiContainer;
  @Input() itemSource: Observable<ViewSet>;
  private zoom: Zoom = new Zoom();
  private viewSet: ViewSet;
  private elements: EditorObject[] = [];

  public posCss = {
    left: 0,
    top: 0,
    width: 0,
    height: 0
  };
  public selectionFrameVisible: boolean = false;
  private renderer: PIXI.WebGLRenderer | PIXI.CanvasRenderer;
  private bump: Bump;
  private stage: PIXI.Container;
  private zoomContainer: PIXI.Container;
  private input: InteractionInput;
  private mousePt: PIXI.Point = new PIXI.Point();

  constructor(private _ngZone: NgZone) {
  }

  ngOnDestroy(): void {
    if (this.renderer) {
      this.renderer.destroy();
    }
  }

  ngOnInit() {
    this.input = new InteractionInput(this.pixiContainer);
    let renderer = this.renderer = PIXI.autoDetectRenderer({
      view: this.pixiContainer.nativeElement,
      width: this.pixiContainer.nativeElement.parentElement.clientWidth,
      height: this.pixiContainer.nativeElement.parentElement.clientHeight,
      transparent: true,
      autoResize: true
    });
    this.bump = new Bump(PIXI);
    let stage = this.stage = new PIXI.Container();
    let zoomContainer = this.zoomContainer = new PIXI.Container();
    zoomContainer.addChild(stage);
    this.addSelectionFrame(renderer, stage);
    this.itemSource.subscribe(viewSet => {
      if (this.viewSet) {
        if (viewSet.viewId != this.viewSet.viewId) {
          this.elements = [];
          stage.removeChildren();
        }
      }
      this.viewSet = viewSet;
      viewSet.elements.forEach(element => {
        if (element instanceof ViewGroup) {
          let group = new GroupObject();
          element.elements.forEach(e => {
            let editorObject = new EditorObject();
            editorObject.x = e.position.x;
            editorObject.y = e.position.y;
            group.add(editorObject);
            this.elements.push(editorObject);
          });

          stage.addChild(group);
          this.elements.push(group);
          let groupChildren = group.getChildren();
          for (let i = 0; i < groupChildren.length - 1; i++) {
            let conn = new Connection(groupChildren[i], groupChildren[i + 1]);
            stage.addChild(conn);
          }
        } else {
          throw new Error('invalid element');
        }
      });
    });

    let ctrlKey = new KeyboardKey(17);

    renderer.plugins.interaction.on('mousedown', ev => {
      let mouseX = ev.data.global.x;
      let mouseY = ev.data.global.y;
      let hitObject = renderer.plugins.interaction.hitTest(ev.data.global, stage);
      if (hitObject instanceof EditorObject) {
        if (ctrlKey.isUp) {
          this.elements.forEach(e => {
            e.selected = false;
          });
        }
        hitObject.selected = true;
        this.elements.filter(e => e.selected).forEach(e => {
          e.startDrag(new PIXI.Point(mouseX - e.x, mouseY - e.y));
        });
      } else if (hitObject === null) {
        this.elements.forEach(e => {
          e.selected = false;
        });
      }
    });
    renderer.plugins.interaction.on('mousemove', ev => {
      this.elements.filter(e => e.isDragging()).forEach(e => {
        e.updatePosition(ev);
      });
    });
    renderer.plugins.interaction.on('mouseup', ev => {
      this.elements.forEach(e => {
        e.stopDrag();
      });
    });

    let ticker = PIXI.ticker.shared;
    ticker.autoStart = false;
    ticker.stop();
    ticker.start();
    ticker.add((deltaTime) => {
      let input = this.input;
      this.mousePt.set(input.mouseX, input.mouseY);
      const gamePt: PIXI.Point = this.stage.toLocal(this.mousePt, this.stage);
      if (input.isDragging) {
        this.zoom.translateBy(this.input.mouseDX, this.input.mouseDY);
        this.input.clearMouseDelta();
        this._updateZoom();
      } else {
        this.zoom.setScaleCenter(gamePt.x, gamePt.y);
        //this.cursorRect.position.set(gamePt.x, gamePt.y);
      }

      if (input.isZoomIn) {
        this._zoomIn(gamePt);
      } else if (input.isZoomOut) {
        this._zoomOut(gamePt);
      }
      renderer.render(zoomContainer);
    });
  }

  private _zoomIn(at: PIXI.Point) {
    this.zoom.zoomBy(0.1, 0.1);
    this._updateZoom();
  }

  private _zoomOut(at: PIXI.Point) {
    this.zoom.zoomBy(-0.1, -0.1);
    this._updateZoom();
  }

  private _updateZoom() {
    const t = this.zoom.Transform;

    this.stage.transform.localTransform.set(t.a, t.b, t.c, t.d, t.tx, t.ty);
    this.zoomContainer.transform.updateTransform(this.stage.transform);
  }


  private addSelectionFrame(renderer, stage) {
    let mouseDownX = null;
    let mouseDownY = null;
    renderer.plugins.interaction.on('mousedown', ev => {
      let hitObject = renderer.plugins.interaction.hitTest(ev.data.global, stage);
      if (hitObject != null) return;
      mouseDownX = ev.data.global.x;
      mouseDownY = ev.data.global.y;
      this.selectionFrameVisible = true;
    });
    renderer.plugins.interaction.on('mousemove', ev => {
      if (!this.selectionFrameVisible) return;
      const x = ev.data.global.x;
      const y = ev.data.global.y;
      const selectWidth = Math.abs(x - mouseDownX);
      const selectHeight = Math.abs(y - mouseDownY);
      const minX = Math.min(ev.data.global.x, mouseDownX);
      const minY = Math.min(ev.data.global.y, mouseDownY);
      this.posCss = {
        "left": minX + this.pixiContainer.nativeElement.clientLeft,
        "top": minY + this.pixiContainer.nativeElement.clientTop,
        "width": selectWidth,
        "height": selectHeight
      };
    });
    renderer.plugins.interaction.on('mouseup', ev => {
      this.posCss = {left: 0, top: 0, width: 0, height: 0};
      this.selectionFrameVisible = false;
    });
    renderer.plugins.interaction.on('mouseupoutside', ev => {
      let hitTest = (container: PIXI.Rectangle, pt: PIXI.Point | PIXI.ObservablePoint) => {
        return container.contains(pt.x, pt.y);
      };
      if (this.selectionFrameVisible) {
        let rect = new PIXI.Rectangle(this.posCss.left, this.posCss.top, this.posCss.left + this.posCss.width, this.posCss.top + this.posCss.height);
        this.elements.forEach(e => {
          e.selected = hitTest(rect, e.getGlobalPosition());
        });
      }
      this.posCss = {left: 0, top: 0, width: 0, height: 0};
      this.selectionFrameVisible = false;
    });
  }

  @HostListener('window:resize')
  onResize(event) {
    if (!this.renderer) {
      return;
    }
    const el = this.pixiContainer.nativeElement.parentElement;
    const w = el.clientWidth;
    const h = el.clientHeight;
    const renderer = this.renderer;
    renderer.view.style.width = w + "px";
    renderer.view.style.height = h + "px";
    renderer.resize(w, h);
  }
}



