import * as PIXI from 'pixi.js';
import InteractionEvent = PIXI.interaction.InteractionEvent;
import {Subject} from "rxjs/Subject";
import {Observable} from "rxjs/Observable";

export class EditorObject extends PIXI.Container {
  public startDrag(offset: PIXI.Point) {
    this._dragOffset = offset;
    this._dragging = true;
  }

  public stopDrag() {
    this._dragOffset = null;
    this._dragging = false;
  }

  set selected(value) {
    this._selected = value;
    this.updateSelectionFrame(value);
  }

  get selected() {
    return this._selected;
  }

  protected updateSelectionFrame(isSelected) {
    if (isSelected && !this._selected) {
      let selectionIndicator = new PIXI.Graphics();
      let b = this.getBounds();
      selectionIndicator.fillAlpha = 1;
      selectionIndicator.lineStyle(1, 0x333300);
      selectionIndicator.drawRect(0, 0, b.width, b.height);
      this.addChild(selectionIndicator);
      this._selectionIndicator = selectionIndicator;
    } else {
      this.removeChild(this._selectionIndicator);
      this._selectionIndicator = null;
    }
  }

  protected _selectionIndicator: PIXI.Graphics;
  protected _dragging: boolean;
  protected _dragOffset: PIXI.Point;

  protected _moveSubject: Subject<PIXI.PointLike> = new Subject();
  public onMove: Observable<PIXI.PointLike> = this._moveSubject.asObservable();

  public moved() {
    this._moveSubject.next(this.getGlobalPosition());
  }

  protected _selected;

  constructor() {
    super();
    this.buttonMode = true;
    this.interactive = true;
    this.createDragAndDropFor(this);
    this.createView();
  }

  protected createView() {
    let shape = new PIXI.Graphics();
    shape.beginFill(200);
    shape.drawCircle(20, 20, 20);
    shape.endFill();
    this.addChild(shape);
  }

  protected createDragAndDropFor(target: EditorObject) {
    target.on("mouseup", (e: InteractionEvent) => {
      target._dragging = false;
      target._dragOffset = null;
    });
    target.on("mousemove", (e: InteractionEvent) => {
      if (target._dragging) {
        this.updatePosition(e);
      }
    })
  }

  public updatePosition(e: InteractionEvent) {
    this.position.x = e.data.global.x - this._dragOffset.x;
    this.position.y = e.data.global.y - this._dragOffset.y;
    this._moveSubject.next(this.getGlobalPosition());
  }

  isDragging() {
    return this._dragging;
  }
}



